.play {
  height: 65vh;
  display: flex;
  justify-content: center;
  padding: 20px;
}

.play-container {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
  gap: 20px;
}

.options {
  width: 150px;
  height: 150px;
  border-radius: 40px;
  font-size: 20px;
  cursor: pointer;
}
